.sh-burger {
	position: relative;
	display: none;
	width: 34px;
	height: 26px;
	cursor: pointer;
	transition-duration: .2s;

	span,
	span::before,
	span::after {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 34px;
		height: 2px;
		margin-top: -1px;
		margin-left: -17px;
		background-color: #fff;
	}

	span::before,
	span::after {
		content: '';
		transition: 0.2s;
	}

	span::before {
		transform: translateY(-12px);
	}

	span::after {
		transform: translateY(12px);
	}

	&--active {
		background: #F5F6FB;

		span {
			height: 0;

			&::before {
				transform: rotate(45deg);
				background: #323340;
			}

			&::after {
				transform: rotate(-45deg);
				background: #323340;
			}
		}
	}
}

.header{
	padding-top: 28px;
	padding-bottom: 28px;
	&__inner{
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	&__list{
		display: flex;
		align-items: center;
		justify-content: space-between;
		&-item{
			font-size: 14px;
			line-height: 17px;
			position: relative;
			&:hover{
				color: $secondary;
				&::before{
					content: '';
					position: absolute;
					bottom: -12px;
					left: 0;
					height: 1px;
					background: $secondary;
					width: 80%;
	
				}
			}
			
			&:not(:last-child){
				margin-right: 25px;
			}
		}
	}
	&__contacts{
		display: flex;
		align-items: center;
		flex-direction: row;
		&-item{
			display: flex;
			align-items: center;
			flex-direction: row;
			>img{
				margin-right: 10px;
			}
			&:not(:last-child){
				margin-right: 25px;
			}
			&:last-child{
				&:hover{
					color: $primary;
					text-decoration: underline;
				}
			}
		}
	}
}