.btn {
    padding-left: 35px;
    padding-right: 20px;
    width: 250px;
    height: 50px;
    background-color: $primary;
    cursor: pointer;
    position: relative;
    overflow: hidden;  
    color: #fff;
    border-left: 5px solid $secondary;
    &:before{
        content: '';
        position: absolute;
        top: 0;
        left: -100%;
        width: 200%;
        height: 100%;
        background: linear-gradient(to right, rgba(221,0,96,1) 0%,rgba(221,0,96,1) 50%,rgba(55,165,140,1) 50%,rgba(55,165,140,1) 100%);
        transition: left .7s;
    }
    &:hover:before{
    left: 0;
    }
    &__content{
        position: relative;
        display: flex;
        align-items: center;
        flex-direction: row;
        justify-content: space-between;
        >img{
            transform: rotate(-90deg);
        }
    }
}